.remote-order {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
  }
  
  .header {
    width: 100%;
    display: flex;
    justify-content: flex-start;
  }
  
  .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  .beer-selection {
    display: flex;
    gap: 10px;
    margin: 20px 0;
  }
  
  .beer-button {
    background-color: orange;
    color: black;
    padding: 10px 20px;
    border: none;
    margin-top: 20px;
    width: 300px;
    height: 300px;
    border-radius: 10px;
    font-weight: 800;
    font-size: 50px;
    cursor: pointer;
  }
  
  .beer-button.selected {
    background-color: yellow;
  }
  
  .quantity-control {
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 20px 0;
  }
  
  .label {
    background-color: orange;
    color: black;
    font-weight: 900;
    padding: 5px 10px;
    font-weight: 900;
    border-radius: 10px;
  }
  
  .quantity-control button {
    background-color: orange;
    color: black;
    border: none;
    padding: 10px;
    border-radius: 10px;
    cursor: pointer;
  }

  .quantity-control button:hover {
    background-color: yellow;
  }
  
  .quantity {
    font-size: 18px;
    margin: 0 10px;
    color: white;
  }
  
  .option-selection {
    display: flex;
    gap: 10px;
    margin: 20px 0;
  }
  
  .option-button {
    background-color: pink;
    color: black;
    padding: 10px 20px;
    border: none;
    font-weight: 900;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .option-button.selected {
    background-color: yellow;
  }
  
  .order-button {
    background-color: orange;
    color: black;
    padding: 10px 20px;
    border: none;
    width: 600px;
    font-weight: 900;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 20px;
  }
  
  /* 반응형 디자인 */
@media (max-width: 768px) {
    .beer-button {
        width: 100px;
        height: 100px;
        font-size: 15px;
    }

    .order-button {
        width: 200px;
      }
  }
  
  @media (min-width: 769px) and (max-width: 1024px) {
    .beer-button {
        width: 200px;
        height: 200px;
        font-size: 30px;
    }

    .order-button {
        width: 400px;
      }
  }